// const barcode = require('./barcode.js');
import barCode from "./barcode.js"
export default {
	name: "tkiBarcode",
	props: {
		unit: {
			type: String,
			default: 'upx'
		},
		val: {
			type: String,
			default: '1234567890'
		},
		opations: {
			type: Object,
			default: function () {
				return {
					format: "CODE39",//选择要使用的条形码类型
					width: 3,//设置条之间的宽度
					height: 100,//高度
					displayValue: true,//是否在条形码下方显示文字
					text: "456",//覆盖显示的文本
					fontOptions: "bold italic",//使文字加粗体或变斜体
					font: "fantasy",//设置文本的字体
					textAlign: "left",//设置文本的水平对齐方式
					textPosition: "top",//设置文本的垂直位置
					textMargin: 5,//设置条形码和文本之间的间距
					fontSize: 15,//设置文本的大小
					background: "#eee",//设置条形码的背景
					lineColor: "#2196f3",//设置条和文本的颜色。
					margin: 15,//设置条形码周围的空白边距
					marginTop: 15,//设置条形码周围的顶部边距
					marginBottom: 15,//设置条形码周围的底部边距
					marginLeft: 15,//设置条形码周围的左边距
					marginRight: 15,//设置条形码周围的右边距
				}
			}
		},
		onval: {
			type: Boolean,
			default: false
		},
		loadMake: {
			type: Boolean,
			default: true
		},
	},
	data() {
		return {
			result: '',
			canvasWidth: 0,
			canvasHeight: 0,
		}
	},
	onUnload: function () {
	},
	methods: {
		_makeCode() {
			let that = this
			new barCode(that, {
				format: "CODE128",//选择要使用的条形码类型
				width: 3,//设置条之间的宽度
				height: 100,//高度
				displayValue: true,//是否在条形码下方显示文字
				text: "123456",//覆盖显示的文本
				// text: "&#20320;&#22909;",//覆盖显示的文本
				textAlign: "center",//设置文本的水平对齐方式
				textPosition: "bottom",//设置文本的垂直位置
				// textPosition: "top",//设置文本的垂直位置
				textMargin: 1,//设置条形码和文本之间的间距
				fontSize: 24,//设置文本的大小
				background: "#eee",//设置条形码的背景
				lineColor: "#2196f3",//设置条和文本的颜色。
				margin: 10,//设置条形码周围的空白边距
				// marginTop: 0,//设置条形码周围的顶部边距
				// marginBottom: 0,//设置条形码周围的底部边距
				marginLeft: 50,//设置条形码周围的左边距
				marginRight: 0,//设置条形码周围的右边距
				cbCanvasSize: function (res) { // 生成条形码的回调
					that.canvasWidth = res.width
					that.canvasHeight = res.height
				},
				cbResult: function (res) { // 生成条形码的回调
					that._result(res)
				},
			});
		},
		_clearCode() {
			this._result('')
		},
		_saveCode() {
			let that = this;
			if (this.result != "") {
				uni.saveImageToPhotosAlbum({
					filePath: that.result,
					success: function () {
						uni.showToast({
							title: '条形码保存成功',
							icon: 'success',
							duration: 2000
						});
					}
				});
			}
		},
		_result(res) {
			this.result = res;
			this.$emit('result', res)
		},
		_result(res) {
			this.result = res;
			this.$emit('result', res)
		},
		_empty(v) {
			let tp = typeof v,
				rt = false;
			if (tp == "number" && String(v) == "") {
				rt = true
			} else if (tp == "undefined") {
				rt = true
			} else if (tp == "object") {
				if (JSON.stringify(v) == "{}" || JSON.stringify(v) == "[]" || v == null) rt = true
			} else if (tp == "string") {
				if (v == "" || v == "undefined" || v == "null" || v == "{}" || v == "[]") rt = true
			} else if (tp == "function") {
				rt = false
			}
			return rt
		}
	},
	mounted: function () {
		if (this.loadMake) {
			if (!this._empty(this.val)) {
				setTimeout(() => {
					this._makeCode()
				}, 0);
			}
		}
	},
}
